.app__works {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.app__work-filter {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;

  margin: 4rem 0 2rem;

  .app__work-filter-item {
    padding: 0.35rem 0.9rem;
    border-radius: 0.5rem;
    background-color: var(--lightwhite-color);
    color: var(--brown-color);
    font-size: 1.3rem;
    font-weight: 800;

    cursor: pointer;
    transition: all 0.3s ease;
    margin: 0.3rem;

    &:hover {
      background-color: var(--yellow-color);
      color: var(--brown-color);
    }

    @media screen and (min-width: 2000px) {
      padding: 1rem 2rem;
      border-radius: 0.85rem;
    }
    @media screen and (max-width: 1200px) {
      font-size: 1.4rem;
      margin: 0.2rem;
    }
    @media screen and (max-width: 930px) {
      font-size: 1.22rem;
      margin: 0.17rem;
    }
    @media screen and (max-width: 768px) {
      font-size: 1.1rem;
      margin: 0.15rem;
    }
    @media screen and (max-width: 450px) {
      font-size: 0.8rem;
      margin: 0.13rem;
    }
    @media screen and (max-width: 300px) {
      font-size: 0.5rem;
      margin: 0.1rem;
    }
  }

  .item-active {
    background-color: var(--yellow-color);
    color: var(--brown-color);
  }
}

.app__work-portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  .app__work-item {
    width: 250px;
    flex-direction: column;

    margin: 2rem;
    padding: 1rem;
    border-radius: 0.5rem;
    background-color: var(--lightwhite-color);
    color: rgb(0, 0, 0);

    cursor: pointer;
    transition: all 0.3s ease;

    &:hover {
      // box-shadow: 0 0 12px rgba(255, 255, 255, 0.25);
      box-shadow: 0 0 7px var(--brown-color);

    }

    @media screen and (min-width: 2000px) {
      width: 450px;
      padding: 1.25rem;
      border-radius: 0.75rem;
    }

    @media screen and (max-width: 300px) {
      width: 100%;
      margin: 1rem;
    }
  }
}

.app__work-img {
  width: 100%;
  height: 230px;

  position: relative;

  img {
    width: 100%;
    height: 100%;
    border-radius: 0.5rem;
    object-fit: cover;
  }

  @media screen and (min-width: 2000px) {
    height: 350px;
  }
}

.app__work-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  border-radius: 0.5rem;
  opacity: 0;
  transition: all 0.3s ease;

  div {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    color: var(--yellow-color);         
    // need_edit

    margin: 1rem;
    font-family: var(--font-base);
    font-weight: 800;
    cursor: pointer;
    transition: all 0.3s ease;

    svg {
      width: 50%;
      height: 50%;
      color: var(--lightwhite-color);
    }
  }
}

.p-textdes {
  font-size: 0.9rem;
  font-weight: 500;
  margin-top: 0.1rem;
}

.app__work-content {
  padding: 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;

  h4 {
    margin-top: 1rem;
    line-height: 1.5;

    @media screen and (min-width: 2000px) {
      margin-top: 3rem;
    }
  }

  .app__work-tag {
    position: absolute;
    padding: 0.4rem 0.6rem;
    border-radius: 4px;
    background-color: var(--brown-color);
    top: -25px;
    @media screen and (max-width: 1200px) {
      padding: 0.4rem 1rem;
      font-size: 1rem;
    }
  
    @media screen and (max-width: 800px) {
      padding: 0.35rem 1rem;
      font-size: 0.8rem;
    }
    
    @media screen and (max-width: 450px) {
      padding: 0.31rem 1rem;
      font-size: 0.7rem;
    }
  }
}
